import {EventEmitter, Injectable} from '@angular/core';
import {Observable} from 'rxjs';
import {SurveyResponses} from '../../pages/survey-response.model';
import {Store} from '@ngrx/store';
import * as surveyActions from '../../pages/store/survey-actions';

@Injectable()
export class PagesService{

  private readonly surveyUrl: string;
  private readonly submitResponseUrl: string;
  public previousQuestion: EventEmitter<number> = new EventEmitter();
  public nextQuestion: EventEmitter<number> = new EventEmitter();
  public nextPageEvent: EventEmitter<string> = new EventEmitter();
  public surveyResponses: SurveyResponses[] = [];
  public sequenceOfQuestionTraversed: number[] = [];

  public stage: string;

  constructor() {

  }

  public persistSurvey(response : SurveyResponses){
    let index = this.surveyResponses.findIndex(surveyResponse => surveyResponse.identifier === response.identifier);
    if (index !== -1) {
      this.surveyResponses[index] = response;
    }else {
      this.surveyResponses.push(response);
    }
  }

  public removeResponsesOfExcludedQuestionIds(payload: any[]) {
    return this.surveyResponses.filter(response => payload.find(questionId => questionId == response.questionId));
  }


  public getResponse(identifier: string){
    console.log(this.surveyResponses);

    let index = this.surveyResponses.findIndex(res => res.identifier == identifier);
    if (index > -1) {
      return this.surveyResponses[index];
    }else {
      return new SurveyResponses();
    }
  }


}
