import {Condition} from '../pages.model';
import {SurveyResponses} from '../survey-response.model';

export class ExpressionBuilder {

    private readonly IS_EQUALS_TO: string = 'Is equals to';
    private readonly IS_NOT_EQUALS_TO: string = 'Is not equals to';
    private readonly IS_LESS_THAN: string = 'Is less than';
    private readonly IS_GREATER_THAN: string = 'Is greater than';
    private readonly IS_LESS_THAN_OR_EQUALS_TO: string = 'Is less than or equals to';
    private readonly IS_GREATER_THAN_OR_EQUALS_TO: string = 'Is grater than or equals to';
    private readonly IS: string = 'Is';
    private readonly IS_NOT: string = 'Is not';
    private readonly IS_ON: string = "Is on";
    private readonly IS_NOT_ON: string = "Is not on";
    private readonly IS_BEFORE: string = "Is before";
    private readonly IS_AFTER: string = "Is after";
    private readonly IS_BEFORE_OR_ON: string = "Is before or on";
    private readonly IS_AFTER_OR_ON: string = "Is after or on";
    private readonly IS_BETWEEN: string = "Is between";
    private readonly IS_BEGINS_WITH: string = "Is begins with";
    private readonly IS_ENDS_WITH: string = "Is ends with";
    private readonly CONTAINS: string = "Contains";
    private readonly DOES_NOT_CONTAINS: string = "Does not contains";

  public build(conditions: Condition[],
                      responses: SurveyResponses[],
                      questionType: string): string{

    let expression: string = '';

    switch (questionType) {
      case 'Drop down':
      case 'Choice':
        for (let condition of conditions) {
          let result = this.evaluateChoiceConditions(condition, responses[responses.findIndex(res => res.questionId == condition._if)]);
          if (condition.relationWithNextCondition != undefined)
          expression += result + ' '+ condition.relationWithNextCondition + ' ';
          else  expression += result + ' ';
        }
        break;

      case 'Yes No':
        for (let condition of conditions) {
          let result = this.evaluateYesNoConditions(condition,responses[responses.findIndex(res => res.questionId == condition._if)]);
          if (condition.relationWithNextCondition != undefined)
            expression += result + ' '+ condition.relationWithNextCondition + ' ';
          else  expression += result + ' ';
        }
        break;

      case 'Rating':
        for (let i = 0; i < conditions.length; i++) {
          let result = this.evaluateRatingConditions(conditions[i],responses[responses.findIndex(res => res.questionId == conditions[i]._if)]);
          console.warn(result);
          if ((i + 1) < conditions.length){
            expression += result + ' ' + conditions[i].relationWithNextCondition+ ' ';

          }else{
            expression += result + ' ';
          }

        }

        break;

      case 'Nps':
        for (let i = 0; i < conditions.length; i++) {
          let result = this.evaluateNPSConditions(conditions[i],responses[responses.findIndex(res => res.questionId == conditions[i]._if)]);
          console.warn(result);
          if ((i + 1) < conditions.length){
            expression += result + ' ' + conditions[i].relationWithNextCondition+ ' ';

          }else{
            expression += result + ' ';
          }

        }

        break;

      case 'Date':
        for (let condition of conditions) {
          let result = this.evaluateDateConditions(condition,responses[responses.findIndex(res => res.questionId == condition._if)]);
          if (condition.relationWithNextCondition != undefined){
            expression += result + ' ' + condition.relationWithNextCondition+ ' ';
          }else{
            expression += result + ' ';
          }
        }
        break;

      case 'Short text':
        for (let condition of conditions) {
          let result = this.evaluateTextConditions(condition,responses[responses.findIndex(res => res.questionId == condition._if)]);
          if (condition.relationWithNextCondition != undefined){
            expression += result + ' ' + condition.relationWithNextCondition+ ' ';
          }else{
            expression += result + ' ';
          }
        }
        break;

      default:
          break;
    }
    return expression;
  }



  private evaluateChoiceConditions(condition: Condition,
                                   currentResponse: SurveyResponses){
    return condition.condition == this.IS ?
      currentResponse.choices.some(choice => choice.choiceId == condition._this) :
      !currentResponse.choices.some(choice => choice.choiceId == condition._this);
  }


  private evaluateRatingConditions(condition: Condition, response: SurveyResponses) {
    console.warn(response);
    switch (condition.condition) {
      case this.IS_EQUALS_TO:
        return (response.rating == condition._this);
      case this.IS_NOT_EQUALS_TO:
        return (response.rating != condition._this);
      case this.IS_LESS_THAN:
        return (response.rating < condition._this);
      case this.IS_GREATER_THAN:
        return (response.rating > condition._this);
      case this.IS_LESS_THAN_OR_EQUALS_TO:
        return (response.rating <= condition._this);
      case this.IS_GREATER_THAN_OR_EQUALS_TO:
        return (response.rating >= condition._this);
      default:
        return false;
    }
  }


  private evaluateNPSConditions(condition: Condition, response: SurveyResponses) {
    console.warn(response);
    switch (condition.condition) {
      case this.IS:
        return RegExp(condition._this).test(response.rating+'');
      case this.IS_NOT:
        return !RegExp(condition._this).test(response.rating+'');
      default:
        return false;
    }
  }

  private evaluateYesNoConditions(condition: Condition,
                                  currentResponse: SurveyResponses) {
    return condition.condition == this.IS ?
      currentResponse.answer == condition._this :
      !currentResponse.answer == condition._this;
  }

  private evaluateDateConditions(condition: Condition,
                                 currentResponse: SurveyResponses) {

    let thisDate = new Date(condition._this);
    let responseDate = new Date(currentResponse.answer);

    switch (condition.condition){

      case this.IS_ON:
        return responseDate == thisDate;

      case this.IS_NOT_ON:
        return responseDate != thisDate;

      case this.IS_BEFORE:
        return responseDate < thisDate;

      case this.IS_AFTER:
        return responseDate > thisDate;

      case this.IS_BEFORE_OR_ON:
        return responseDate <= thisDate;

      case this.IS_AFTER_OR_ON:
        return responseDate >= thisDate;

        //todo Expression for date between range.
      case this.IS_BETWEEN:
        return true;
      default:
        return false;
    }
  }

  private evaluateTextConditions(condition: Condition,
                                 currentResponse: SurveyResponses) {
        switch(condition.condition){

          case this.IS_EQUALS_TO:
            return currentResponse.answer == condition._this;

          case this.IS_BEGINS_WITH:
            return currentResponse.answer.startsWith(condition._this);

          case this.IS_ENDS_WITH:
            return currentResponse.answer.endsWith(condition._this);

          case this.CONTAINS:
            return currentResponse.answer.includes(condition._this);

          case this.DOES_NOT_CONTAINS:
            return !currentResponse.answer.includes(condition._this);

          default:
            return false;
        }
  }


}
