import {SurveyResponses} from '../survey-response.model';

export class MandatoryQuestionValidation {


  constructor() {
  }

  public isQuestionAnswered(questionType: string,
                            response: SurveyResponses): boolean{
    console.log('Inside isQuestionAnswered!');
    console.log('Question type: '+questionType);

    if (response == undefined)
      return false;

    switch (questionType) {

      case 'Address':
        return this.isAddressFormAnswered(response);

      case 'Choice':
        return this.isChoiceQuestionAnswered(response);

      case 'Contact form':
        return this.isContactFormAnswered(response);

      case 'Date':
        return this.isDateQuestionAnswered(response);

      case 'Drop Down':
        return this.isDropDownQuestionAnswered(response);

      case 'Email':
        return this.isEmailQuestionAnswered(response);

      case 'Short text':
      case 'Long text':
        return this.isTextQuestionAnswered(response);

      case 'Nps':
      case 'Rating':
        return this.isRatingQuestionAnswered(response);

      case 'Statement':
        return this.isStatementQuestionAnswered(response);

      case 'Yes No':
      case 'Legal':
        return this.isYesNoQuestionAnswered(response);

      default: return false;
    }
  }

  private isAddressFormAnswered(response: SurveyResponses) {
    return false;
  }

  private isContactFormAnswered(response: SurveyResponses) {
    return false;
  }

  private isChoiceQuestionAnswered(response: SurveyResponses) {
    return response.choices != undefined && response.choices.length > 0;
  }


  private isDateQuestionAnswered(response: SurveyResponses) {
    return response.answer != undefined && response.answer.length > 0;
  }

  private isDropDownQuestionAnswered(response: SurveyResponses) {
    return response.answerId != undefined;
  }

  private isEmailQuestionAnswered(response: SurveyResponses) {
    return response.answer != undefined && response.answer.length > 0;
  }

  private isTextQuestionAnswered(response: SurveyResponses) {
    return response.answer != undefined && response.answer.length > 0;
  }

  private isRatingQuestionAnswered(response: SurveyResponses) {
    return response.rating != undefined && response.rating > -1;
  }

  private isStatementQuestionAnswered(response: SurveyResponses) {
    return false;
  }

  private isYesNoQuestionAnswered(response: SurveyResponses) {
    return response.answer != undefined && response.answer.length > 0;
  }
}
