export class SurveyResponseModel {

  public surveyId: string;
  public channel: string;
  public surveyResponses: SurveyResponses[];

}

export class ResponseMetaInformation {
  public startedAt: string;
  public completedAt: string;
  public noOfQuestionAnswered: string;
  public platform: string;
  public surveyToken: string;
  public timeTakenInSeconds: string;
}

export class SurveyResponses {
  public identifier?: string;
  public question?: string;
  public questionType?: string;
  public questionId?: any;
  public answer?: string;
  public answerId?: any;
  public rating?: number;
  public contactInfo?: any[];
  public addressInfo?: AddressInfo;
  public choices?: any[];
}



export class AddressInfo {
  public street1: string;
  public street2: string;
  public city: string;
  public state: string;
  public country: string;
  public zipCode: number;
}
