import * as SurveyActions from './survey-actions';
import {SurveyResponses} from '../survey-response.model';

let surveyResponseInitialState: SurveyResponses[] = [];

const INITIAL_STATE = {
  response: surveyResponseInitialState
};



export function surveyReducers(state = INITIAL_STATE,
                               action: SurveyActions.Actions) {
  console.log(state);
  switch (action.type) {
    case SurveyActions.SAVE_CHOICE:
     return {
       ...state,
       response: getResponse(state.response,action.payload)
     };

    case SurveyActions.SAVE_NPS:
      return {
        ...state,
        response: getResponse(state.response,action.payload)
      };

    case SurveyActions.SAVE_YES_NO:
      return {
        ...state,
        response: getResponse(state.response,action.payload)
      };

    case SurveyActions.SAVE_RATING:
      return {
        ...state,
        response: getResponse(state.response,action.payload)
      };

    case SurveyActions.SAVE_DATE:
      return {
        ...state,
        response: getResponse(state.response,action.payload)
      };

    case SurveyActions.SAVE_EMAIL:
      return {
        ...state,
        response: getResponse(state.response,action.payload)
      };

    case SurveyActions.SAVE_PHONE:
      return {
        ...state,
        response: getResponse(state.response,action.payload)
      };

    case SurveyActions.SAVE_AGREE_DISAGREE:
      return {
        ...state,
        response: getResponse(state.response,action.payload)
      };

    case SurveyActions.SAVE_LONG_TEXT:
      return {
        ...state,
        response: getResponse(state.response,action.payload)
      };

    case SurveyActions.SAVE_SHORT_TEXT:
      return {
        ...state,
        response: getResponse(state.response,action.payload)
      };

    case SurveyActions.SAVE_ADDRESS:
      return {
      ...state,
      response: getResponse(state.response,action.payload)
    };

    case SurveyActions.REMOVE_RESPONSES:
      return {
        ...state,
        response: removeResponse(state.response, action.payload)
      };

    case SurveyActions.REMOVE_RESPONSES_OF_EXCLUDED_QUESTION_IDS:
      return {
        ...state,
        response: removeResponsesOfExcludedQuestionIds(state.response, action.payload)
      };

    case SurveyActions.RESET_RESPONSE:
      return INITIAL_STATE;

    default:
      console.log(JSON.stringify(state));
      return state;
  }

}

function removeResponse(responses: SurveyResponses[], payload: number) {
  responses.splice(responses.findIndex(response => response.questionId == payload),1);
  return responses;
}

function getResponse(response: SurveyResponses[],
                     payload: SurveyResponses): SurveyResponses[]{
  let index = response.findIndex(question => question.identifier == payload.identifier);
  if (index == undefined || index < 0){
    return [...response,payload]
  }else{
    response[index] = payload;
    return response;
  }
}

function removeResponsesOfExcludedQuestionIds(responses: SurveyResponses[], payload: number[]) {
  return responses.filter(response => payload.find(questionId => questionId == response.questionId));
}

